import { Card, CardHeader, CardContent } from "../../../../../components/Card";
import { Badge } from "../../../../../components/Badge";
import { candidateProfileData } from "@/lib/models/candidate";
import { useRouter } from "next/router";
import { useState } from "react";
import MainLayout from "@/components/layout";
import { Popup } from "@/components/popup";
import InterviewForm from "@/components/updateInterview";
export default function CandidateInterviews() {
  const router = useRouter();
  const jobId = router.query.jobid;
  const candidateId=router.query.id
  console.log(jobId,candidateId);
  const [addRoundEnabled, setAddRoundEnabled] = useState(false);
  const [updateRoundEnabled, setUpdateRoundEnabled] = useState(false);
  const personalDetails = {
    PreferredJobType: "Remote",
    Gender: "Male",
    NoticePeriod: "Immediate Joiner",
    MaritalStatus: "Unmarried",
    Location: "Chennai",
    Education: "B.sc Physics",
    Experience: "Fresher",
    ExpectedSalary: "2,50,000 - 3,00,000",
  };
  return (
    <MainLayout>
      <div className="space-y-6">
        {/* Profile Info Section */}
        <section>
          <h2 className="text-xl font-semibold mb-4">Profile Info</h2>
          <Card className="border shadow-sm">
            <CardHeader>
              <div>
                <h3 className="text-xl font-semibold mb-4">
                  {candidateProfileData[0].firstName}{" "}
                  {candidateProfileData[0].lastName}
                </h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                  <div className="flex items-center gap-6">
                    <span className="text-gray-500">
                      {candidateProfileData[0].designation}
                    </span>
                    <span>{candidateProfileData[0].companyName}</span>
                  </div>
                </div>
              </div>
            </CardHeader>
            <CardContent className="bg-white">
              <div className="grid grid-cols-4 gap-4 mb-4 text-sm">
                {personalDetails &&
                  Object.entries(personalDetails).map(([key, value]) => (
                    <div key={key} className="space-y-4">
                      <h3 className="font-semibold text-sm">{key}</h3>
                      <span className="text-gray-600">{value}</span>
                    </div>
                  ))}
              </div>
            </CardContent>
          </Card>
        </section>

        {/* Job Info Section */}
        <section>
          <h2 className="text-xl font-semibold mb-4">Job Info</h2>
          <Card className="border shadow-sm">
            <CardHeader>
              <div className="space-y-4">
                <h3 className="text-xl font-semibold">
                  {candidateProfileData[0].techRole}
                </h3>
                <p className="text-blue-500 text-md font-semibold">
                  {candidateProfileData[0].companyName}
                </p>
              </div>
            </CardHeader>
            <CardContent className="bg-gray-50">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div className="space-y-4">
                  <div>
                    <h4 className="font-semibold text-sm mb-1">Experience</h4>
                    <p className="text-gray-600">0-3 Yrs</p>
                  </div>
                  <div>
                    <h4 className="font-semibold text-sm mb-1">Location</h4>
                    <p className="text-gray-600">Gurugram</p>
                  </div>
                </div>
                <div className="space-y-4">
                  <div>
                    <h4 className="font-semibold text-sm mb-1">Salary</h4>
                    <p className="text-gray-600">1-3.75 Lacs PA</p>
                  </div>
                  <div>
                    <h4 className="font-semibold text-sm mb-1">Job Code</h4>
                    <p className="text-gray-600">Job Code: XYZ</p>
                  </div>
                </div>
                <div className="space-y-4">
                  <div>
                    <h4 className="font-semibold text-sm mb-1">
                      Skills Required
                    </h4>
                    <p className="text-gray-600">Java, SQL, Spring Boot</p>
                  </div>
                  <div>
                    <h4 className="font-semibold text-sm mb-1">Inserted By</h4>
                    <p className="text-gray-600">Pranoti</p>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </section>

        {/* Interviews Section */}
        <section>
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-semibold">Interviews</h2>
            <div className="flex items-center gap-2">
              <span className="text-gray-600 font-semibold">
                Over all status
              </span>
              <Badge variant="success">Selected</Badge>
            </div>
          </div>

          <div className="space-y-4">
            {/* Round 1 */}
            <Card>
              <CardHeader className="mb-2">
                <h3 className="font-semibold text-lg">Round 1</h3>
                <button
                  className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded" onClick={() => setUpdateRoundEnabled(true)}>
                  Update
                </button>
              </CardHeader>
              {updateRoundEnabled && (
                <Popup onClose={() => setUpdateRoundEnabled(false)}>
                  <InterviewForm className="bg-white relative mt-12"/>
                </Popup>
              )}
              <CardContent className="bg-white border shadow-sm">
                <div className="flex justify-between items-start mb-6">
                  <div className="flex items-center gap-12">
                    <div>
                      <h4 className="font-semibold text-lg">
                        Technical Call Round
                      </h4>
                      <p className="text-sm text-gray-500">23/11/24</p>
                    </div>

                    <Badge className="text-white">Via Call</Badge>
                  </div>

                  <Badge variant="success">Passed</Badge>
                </div>

                <div className="grid grid-cols-2 gap-6 md:grid-cols-4">
                  <div>
                    <p className="text-sm text-black font-semibold">
                      Interviewer Name
                    </p>
                    <p className="text-gray-700">Pranoti</p>
                  </div>
                  <div>
                    <p className="text-sm text-black font-semibold">
                      Technology Interviewed
                    </p>
                    <p className="text-gray-700">Java,Python,React JS</p>
                  </div>
                  <div>
                    <p className="text-sm text-black font-semibold">
                      Tech Rating
                    </p>
                    <p className="text-gray-700">8/10</p>
                  </div>
                  <div>
                    <p className="text-sm text-black font-semibold">
                      Soft skill Rating
                    </p>
                    <p className="text-gray-700">9/10</p>
                  </div>
                </div>

                <div className="mt-4">
                  <p className="text-sm text-black font-semibold">Remarks</p>
                  <p className="text-gray-700">Excellent Technical Knowledge</p>
                </div>
              </CardContent>
            </Card>

            {/* Round 2 and 3 would be similar to Round 1, so I'm omitting them for brevity */}

            <div className="flex justify-center mt-8">
              <button
                className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
                onClick={() => setAddRoundEnabled(true)}>
                Add New Round
              </button>
            </div>
          </div>
        </section>
        {addRoundEnabled && (
          <Popup onClose={() => setAddRoundEnabled(false)}>
            <section className="items-center justify-center bg-white m-4 top-4 h-screen relative p-2">
                <CardHeader className="bg-zinc-200">
                  <h1 className="font-semibold text-lg">
                    Add New Round
                  </h1>
                </CardHeader>
                <CardContent>
                  <div>
                    <div>
                      <h1 className="font-semibold">Round no</h1>
                      <div className="mb-4 mt-4 flex flex-wrap items-center gap-4">
                        <input
                          className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                          id="interview_round"
                          type="number"
                          placeholder="Enter Round Number"
                        />
                      </div>
                    </div>

                    <div>
                      <h1 className="font-semibold">Round Name</h1>
                      <div className="mb-4 mt-4 flex items-center gap-4">
                        <input
                          className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                          id="current_salary"
                          type="text"
                          placeholder="Enter Round Name"
                        />
                      </div>
                    </div>

                    <div>
                      <h1 className="font-semibold">Interview Date</h1>
                      <div className="mb-4 mt-4 flex items-center gap-4">
                        <input
                          className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                          id="qualification"
                          type="date"
                        />
                      </div>
                    </div>

                    <div>
                      <h1 className="font-semibold">Interview Mode</h1>
                      <div className="mb-4 mt-4 flex items-center gap-4">
                        <input
                          className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                          id="expected_salary"
                          type="text"
                          placeholder="Enter Interview Mode"
                        />
                      </div>
                    </div>
                    <div>
                      <h1 className="font-semibold">Interviewer Name</h1>
                      <div className="mb-4 mt-4 flex items-center gap-4">
                        <input
                          className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                          id="expected_salary"
                          type="text"
                          placeholder="Enter Interviewer Name"
                        />
                      </div>
                    </div>
                    <div className="space-x-4">
                      <button className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded">
                        Submit
                      </button>
                      <button
                        className="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded"
                        onClick={() => setAddRoundEnabled(false)}
                      >
                        Cancel
                      </button>
                    </div>
                  </div>
                </CardContent>
            </section>
          </Popup>
        )}
      </div>
    </MainLayout>
  );
}
